import _ from '../../../util/underscore-wrapper';
import BaseView, { BaseViewInstance } from '../../BaseView';
import FormUtil from '../helpers/FormUtil';

export interface ToolbarPublic {
  addButton(parmas, options?);
  buttonOrder: string[];
}

interface ToolbarInternal extends Pick<BaseViewInstance, 'className' | 'initialize'> {
  __getButtonOrder(options);
}

export interface ToolbarInstance extends ToolbarPublic, BaseViewInstance {}

export interface ToolbarConstructor<I extends ToolbarInstance = ToolbarInstance> extends Backbone.View {
  new(attributes?, options?): I;
  extend<C = ToolbarConstructor>(properties: any, classProperties?: any): C;
}

const proto: ToolbarPublic & ToolbarInternal = {
  className: 'o-form-button-bar',
  buttonOrder: ['previous', 'save', 'cancel'],

  initialize: function (this: ToolbarInstance & ToolbarInternal, options) {
    const buttonConfigs = {
      previous: {
        type: 'previous'
      },
      save: {
        type: 'save',
        text: _.resultCtx(options, 'save', this),
        id: _.resultCtx(options, 'saveId', this),
        className: _.resultCtx(options, 'saveClassName', this)
      },
      cancel: {
        type: 'cancel',
        text: _.resultCtx(options, 'cancel', this)
      }
    };
    this.__getButtonOrder(options).forEach((buttonName) => {
      this.addButton(buttonConfigs[buttonName]);
    });
  },

  /**
   * Adds a buttomn to the toolbar
   * @param {Object} params button parameters
   * @param {Object} options {@link Okta.View#add} options
   */
  addButton: function (this: ToolbarInstance, params, options?) {
    return this.add(FormUtil.createButton(params), options);
  },

  __getButtonOrder: function (this: ToolbarInstance, options) {
    const buttonOrder = _.resultCtx(options, 'buttonOrder', this, this.buttonOrder);
    const buttonsToSkip = [];
    if (options.noSubmitButton) {
      buttonsToSkip.push('save');
    }
    if (options.noCancelButton) {
      buttonsToSkip.push('cancel');
    }
    if (!options.hasPrevStep) {
      buttonsToSkip.push('previous');
    }
    return _.without(buttonOrder, ...buttonsToSkip);
  }
};

export default BaseView.extend(proto) as ToolbarConstructor;
