import _ from './underscore-wrapper';
import Model, { ModelInstance, ModelConstructor } from '../models/Model';

interface SettingsModelPublic {
  hasFeature(featureName: string): boolean;
  hasAnyFeature(featureArray);
  hasPermission(permission);
  isDsTheme();
}

interface SettingsModelInternal {
  local();
  constructor();
}
export interface SettingsModelInstance extends SettingsModelPublic, ModelInstance {
  features: string[];
}

export interface SettingsModelConstructor extends ModelConstructor {
  new(attributes?, options?): SettingsModelInstance;
  extend<S = SettingsModelConstructor>(properties: any, classProperties?: any): S;
}

/**
 * @class SettingsModel
 * @extends {Okta.Model}
 * @private
 */
const props: SettingsModelPublic & SettingsModelInternal = {
  local() {
    const settings = (window.okta && window.okta.settings) || {};
    const theme = (window.okta && window.okta.theme) || '';

    return {
      orgId: ['string', false, settings.orgId],
      orgName: ['string', false, settings.orgName],
      serverStatus: ['string', false, settings.serverStatus],
      persona: ['string', false, settings.persona],
      isDeveloperConsole: ['boolean', false, settings.isDeveloperConsole],
      isPreview: ['boolean', false, settings.isPreview],
      permissions: ['array', true, settings.permissions || []],
      theme: ['string', false, theme]
    };
  },

  constructor(this: SettingsModelInstance) {
    Model.apply(this, arguments);
    this.features = window._features || [];
  },

  /**
   * Checks if the user have a feature flag enabled (Based of the org level feature flag)
   * @param  {String}  feature Feature name
   * @return {Boolean}
   */
  hasFeature(this: SettingsModelInstance, feature) {
    return _.contains(this.features, feature);
  },

  /**
   * Checks if any of the given feature flags are enabled (Based of the org level feature flags)
   * @param  {Array}  featureArray Features names
   * @return {Boolean} true if any of the give features are enabled. False otherwise
   */
  hasAnyFeature(this: SettingsModelInstance, featureArray) {
    return _.some(featureArray, this.hasFeature, this);
  },

  /**
   * Checks if the user have a specific permission (based on data passed from JSP)
   * @param  {String}  permission Permission name
   * @return {Boolean}
   */
  hasPermission(this: SettingsModelInstance, permission) {
    return _.contains(this.get('permissions'), permission);
  },

  /**
   * Checks if the org has ds theme set
   * @return {Boolean}
   */
  isDsTheme(this: SettingsModelInstance) {
    return this.get('theme') === 'dstheme';
  }
};

export default Model.extend(props) as SettingsModelConstructor;
