import _ from '../util/underscore-wrapper';
import FrameworkModelImpl, { FrameworkModelInstance, FrameworkModelConstructor } from '../framework/Model';

export interface ModelInstance extends FrameworkModelInstance {
  secureJSON: boolean;
}
export interface ModelConstructor<I extends ModelInstance = ModelInstance> extends FrameworkModelConstructor {
  new(attributes?, options?): I;
  extend<S = ModelConstructor>(properties: any, classProperties?: any): S;
}

/**
 * Wrapper around the more generic {@link src/framework/Model} that
 * contains Okta-specific logic.
 * @class module:Okta.Model
 * @extends src/framework/Model
 */
const constructor: ModelConstructor = FrameworkModelImpl.extend(
  /** @lends module:Okta.Model.prototype */ {
    /**
     * Is the end point using the legacy "secureJSON" format
     * @type {Function|Boolean}
     */
    secureJSON: false,

    _builtInLocalProps: {
      __edit__: 'boolean',
      __pending__: 'boolean'
    },

    constructor: function () {
      this.local = _.defaults({}, _.result(this, 'local'), this._builtInLocalProps);

      FrameworkModelImpl.apply(this, arguments);

      if (_.result(this, 'secureJSON')) {
        this.sync = _.wrap(this.sync, function (sync, method, model, options) {
          return sync.call(this, method, model, _.extend({ dataType: 'secureJSON' }, options));
        });
      }
    }
  }
) as ModelConstructor;
export default constructor;
